import React from 'react'
import Home from './Home'
import Projects from './Projects'
import Profile from './Profile'
import Contact from './Contact'
import projicon from './project.jpg'
import homeicon from './home.jpg'
import contacticon from './contact.jpg'
import profileicon from './profile.png'
import skillsicon from './skills.jpg'
import Skills from './Skills'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
function MainBar() {
    return (
        <div className='MainBar'>
            <div style={{display:'block',textAlign:'center'}}>
        <div style={{display:'inline-block',paddingLeft:'20px'}}>
       <Link  to='/' className='Links'><img className='headicons' src={homeicon}></img></Link>
       <label style={{color:'white',display:'block',fontWeight:'bold'}}>Home</label>
       </div>
       <div style={{display:'inline-block',paddingLeft:'20px'}}> 
       <Link to='/Profile' className='Links'><img className='headicons' src={profileicon}></img></Link>
       <label style={{color:'white',display:'block',fontWeight:'bold'}}>Profile</label>
       </div>
       <div style={{display:'inline-block',paddingLeft:'20px'}}>
       <Link  to='/Skills' className='Links'><img className='headicons' src={skillsicon}></img></Link>
       <label style={{color:'white',display:'block',fontWeight:'bold'}}>Skills</label>
       </div>
       <div style={{display:'inline-block',paddingLeft:'20px'}}>
       <Link  to='/Projects' className='Links'><img className='headicons' src={projicon}></img></Link>
       <label style={{color:'white',display:'block',fontWeight:'bold'}}>Portfolio</label>
       </div>
       <div style={{display:'inline-block',paddingLeft:'11px'}}> 
       <Link to='/Contact' className='Links'><img className='headicons' src={contacticon}></img></Link>
       <label style={{color:'white',display:'block',fontWeight:'bold'}}>Contact</label>
       </div>
       </div>
       <Route exact path='/'
       component={Home}/>
       <Route exact path='/Projects'
       component={Projects}/>
       <Route exact path='/Contact'
       component={Contact}/>
       <Route exact path='/Profile'
       component={Profile}/>
        <Route exact path='/Skills'
       component={Skills}/>
        </div>
    )
}

export default MainBar
