import React, { Component } from 'react'

export class Contact extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             Comments:[{name:"Tushar",comment:"Good"},{name:"Rahul",comment:"VGood"},{name:"Yo",comment:"GoodV"}],
             user:"",
             review:""
        }
    }
    handleChange=event=>{
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submit=()=>{
        var lis=this.state.Comments
        lis.length <5?lis.push({name:this.state.user,comment:this.state.review}):lis=[{name:this.state.user,comment:this.state.review}]
        this.setState({
            Comments:lis
            
    })
    }
    
    render() {
        return (
            <div className='Contact'>
            <div style={{display:'inline-block',verticalAlign:'Top',textAlign:'center',width:'45%'}}>
           <p><span style={{color:'white',fontFamily:'Cursive',fontSize:'2vw',fontWeight:'bolder',textShadow:'2px 1px black'}}>You can post a comment below with your name</span></p>
           <input value={this.state.user} name="user" onChange={this.handleChange} placeholder='Enter your name here' style={{width:'12vw',fontSize:'1.2vw'}}></input><br></br>
           <input value={this.state.review} name="review" onChange={this.handleChange} maxLength='100' placeholder='Comment(Limit:100)' style={{width:'12vw',height:'10vw',marginTop:'10px',marginBottom:'10px',fontSize:'1.2vw'}}></input><br></br>
           <button style={{width:'7vw',fontSize:'1.2vw'}} onClick={this.submit}>Submit</button>
           <p><span style={{color:'white',fontFamily:'Cursive',fontSize:'2vw',fontWeight:'bolder',textShadow:'2px 1px black'}}> Comments:</span></p>
           <ul style={{color:'white'}}>
        {this.state.Comments.map((item)=><li style={{fontSize:'1.5vw',textAlign:'left'}}>{item.name} commented {item.comment}</li>)}
           </ul>
            </div>
            <div style={{display:'inline-block',marginLeft:'10%',width:'45%',textAlignLast:'left'}}>
           <p><span style={{color:'white',fontFamily:'Arial',fontSize:'2vw',fontWeight:'bolder',textShadow:'2px 1px black',backgroundColor:'black',fontSize:'3vw'}}>Contact Info</span></p>
           <span style={{color:'white',fontWeight:'bold',backgroundColor:'black',fontSize:'1.5vw'}}>Email:</span> <span style={{color:'lightblue',fontSize:'1.25vw'}}> tushargoyal7776@gmail.com</span><br></br>
            <span style={{color:'white',fontWeight:'bold',backgroundColor:'black',fontSize:'1.5vw'}}>Home:</span><span style={{color:'lightblue',fontSize:'1.25vw'}}> Sector-52, 122003 Gurgaon</span><br></br>
            <span style={{color:'white',fontWeight:'bold',backgroundColor:'black',fontSize:'1.5vw'}}>Phone:</span> <span style={{color:'lightblue',fontSize:'1.25vw'}}>8569870282</span><br></br>
            <span style={{color:'white',fontWeight:'bold',backgroundColor:'black',fontSize:'1.5vw'}}>City:</span> <span style={{color:'lightblue',fontSize:'1.25vw'}}> Gurgaon</span><br></br>
            <span style={{color:'white',fontWeight:'bold',backgroundColor:'black',fontSize:'1.5vw'}}>State:</span> <span style={{color:'lightblue',fontSize:'1.25vw'}}> Haryana</span>
            </div>
        </div>
        )
    }
}

export default Contact