import React from 'react'
import resume from './sample.pdf'
import resumebutton from './resume.jpg'
import intlink from './intlink.jpg'
function RightBar() {
    return (
        <div className='RightBar'>
        <img style={{height:'100px',width:'15vw',height:'7vw',borderRadius:'20%',textAlign:'center'}} src={intlink}></img>
            <div style={{paddingTop:'20px',display:'block',justifyContent:'center',textAlign:'center'}}>
            <a href={resume}><img className='RightLinks'  src={resumebutton}></img></a>
            </div>
        </div>
    )
}

export default RightBar
